@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
body,
html {
  height: 100%;
  margin: 0;
  background: #252830;
  display: flex;
  justify-content: center;
  align-items: center; }

* {
  box-sizing: border-box;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  font-family: "Lato"; }

.app {
  padding: 1rem;
  padding-bottom: calc(1rem - 5px);
  background: #fff;
  box-shadow: -8px 8px rgba(0, 0, 0, 0.2);
  max-width: 83.33vw; }
  .app .row {
    margin-bottom: 1rem; }
    .app .row:last-child {
      margin: 0; }
    .app .row .label {
      text-transform: capitalize;
      font-size: 18px;
      padding: 5px 15px;
      letter-spacing: 1px;
      font-weight: 400;
      color: #242424; }
    .app .row .custom-input {
      color: #141414;
      width: 100%;
      margin: 0;
      padding: 0;
      font-size: 24px;
      padding: 10px 15px;
      text-overflow: ellipsis;
      text-transform: capitalize;
      border-radius: 10px;
      border: none;
      background: none;
      outline: none !important;
      box-shadow: none !important;
      font-weight: 700; }
      .app .row .custom-input:hover, .app .row .custom-input:focus {
        background: #e9e9e9; }
      .app .row .custom-input[readonly] {
        cursor: not-allowed; }
      .app .row .custom-input::-webkit-input-placeholder {
        font-weight: 400; }
      .app .row .custom-input::-moz-placeholder {
        font-weight: 400; }
      .app .row .custom-input:-ms-input-placeholder {
        font-weight: 400; }
      .app .row .custom-input::-ms-input-placeholder {
        font-weight: 400; }
      .app .row .custom-input::placeholder {
        font-weight: 400; }

